body {
  margin: 0;
}

li {
  list-style: none;
}

h2, p, ul {
  margin: 0;
  padding: 0;
}

.backdrop {
  width: 100%;
  height: 100%;
  z-index: 10;
  background-color: rgba(0, 0, 0, .4);
  padding-top: 80px;
  transition: visibility .25s cubic-bezier(.4, 0, .2, 1), opacity .25s cubic-bezier(.4, 0, .2, 1);
  position: fixed;
  top: 0;
  left: 0;
}

.is-hidden {
  visibility: hidden;
  opacity: 0;
  pointer-events: none;
}

.modal-students {
  width: 800px;
  height: 450px;
  background: linear-gradient(135deg, #767681, #222235) 0 0 / contain no-repeat;
  border-radius: 25px;
  margin-left: auto;
  margin-right: auto;
  display: flex;
  position: relative;
  box-shadow: 0 8px 30px rgba(66, 60, 62, .1);
}

@media screen and (max-width: 479px) {
  .modal-students {
    width: 300px;
  }
}

@media screen and (min-width: 480px) {
  .modal-students {
    width: 400px;
  }
}

@media screen and (min-width: 768px) {
  .modal-students {
    width: 700px;
  }
}

@media screen and (min-width: 1280px) {
  .modal-students {
    width: 800px;
  }
}

.students-button__close {
  width: 35px;
  height: 35px;
  cursor: pointer;
  fill: #8991b8;
  background-color: rgba(0, 0, 0, 0);
  border: 1px solid #8991b8;
  border-radius: 50%;
  justify-content: center;
  align-items: center;
  transition: fill .25s cubic-bezier(.4, 0, .2, 1), border-color .25s cubic-bezier(.4, 0, .2, 1);
  display: flex;
  position: absolute;
  top: 10px;
  right: 7px;
}

.students-button__close:hover, .students-button__close:focus {
  border: 1px solid #b4b1b1;
}

.modal-students__close-icon {
  width: 25px;
  height: 25px;
  transition: fill .25s cubic-bezier(.4, 0, .2, 1), border-color .25s cubic-bezier(.4, 0, .2, 1);
}

.modal-students__close-icon:hover, .modal-students__close-icon:focus {
  fill: #b4b1b1;
}

.students-box__list {
  scroll-snap-type: x mandatory;
  scroll-padding: 24px;
  display: flex;
  overflow-x: scroll;
}

.student-box__item {
  scroll-snap-align: start;
  flex: 0 0 50%;
  margin-top: 30px;
}

@media screen and (max-width: 767px) {
  .student-box__item {
    flex-basis: 100%;
  }
}

@media screen and (min-width: 768px) {
  .student-box__item {
    flex-basis: 50%;
  }
}

.students-box__outside {
  width: 220px;
  border: 2px solid #4a4a6b;
  border-radius: 50%;
  margin: 0 auto 10px;
  padding: 7px;
}

.students-box__inside {
  padding-left: 10px;
}

.student-foto {
  width: 200px;
  height: 200px;
  border-radius: 50%;
}

.students-info {
  flex-direction: column;
  justify-content: center;
  align-items: center;
  padding-right: 10px;
  display: flex;
}

.students-info__title {
  letter-spacing: .1em;
  color: #8991b8;
  border-bottom: 2px solid #8991b8;
  margin-bottom: 12px;
  padding-bottom: 5px;
  font-family: Roboto;
  font-size: 20px;
  font-weight: 500;
  line-height: 1.2;
}

.students-info__text {
  letter-spacing: .1em;
  color: #fff;
  margin-bottom: 10px;
  font-family: Roboto;
  font-size: 20px;
  font-weight: 500;
  line-height: 1.2;
}

.students-info__about {
  letter-spacing: .1em;
  color: #b4b1b1;
  text-align: center;
  margin-bottom: 10px;
  font-family: Roboto;
  font-size: 18px;
  font-weight: 500;
  line-height: 1.2;
}

.social {
  justify-content: center;
  display: flex;
}

.social__link {
  width: 60px;
  height: 60px;
  fill: #b4b1b1;
  box-sizing: border-box;
  object-fit: contain;
  border-radius: 50%;
  justify-content: center;
  align-items: center;
  margin: 0;
  padding: 0;
  transition: fill .25s cubic-bezier(.4, 0, .2, 1), background-color .25s cubic-bezier(.4, 0, .2, 1);
  display: flex;
}

.social__link:active, .social__link:hover, .social__link:focus {
  fill: #8991b8;
  box-sizing: border-box;
  border-radius: 50%;
}

.modal {
  width: 700px;
  height: 450px;
  background: linear-gradient(135deg, #767681, #222235) 0 0 / contain no-repeat;
  border-radius: 50px;
  margin-left: auto;
  margin-right: auto;
  position: relative;
  box-shadow: 0 8px 30px rgba(66, 60, 62, .1);
}

@media screen and (max-width: 479px) {
  .modal {
    width: 300px;
  }
}

@media screen and (min-width: 480px) {
  .modal {
    width: 400px;
  }
}

@media screen and (min-width: 768px) {
  .modal {
    width: 700px;
  }
}

.prev, .next {
  width: 40px;
  height: 40px;
  color: #333;
  cursor: pointer;
  background-color: #eee;
  border: none;
  border-radius: 50%;
  outline: none;
  justify-content: center;
  align-items: center;
  font-size: 30px;
  transition: all .3s;
  display: flex;
  position: absolute;
  top: 50%;
  transform: translateY(-50%);
}

.prev:hover, .next:hover {
  opacity: .8;
}

.prev {
  left: 0%;
}

.next {
  right: 0%;
}

.slider-container {
  width: 100%;
  height: 100%;
}

.slide {
  width: 100%;
  height: 100%;
  align-items: center;
  padding-top: 25px;
  display: none;
}

.slide.active {
  display: block;
}

.slide.active .students-box__outside, .slide.active .students-info {
  animation: fade-in .8s;
}

@keyframes fade-in {
  from {
    opacity: 0;
    transform: translateX(-20px);
  }

  to {
    opacity: 1;
    transform: translateX(0);
  }
}

